.route.circle
{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    /* display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1em; */
}

.route.circle .split-container > .sidebar
{
    /* position: absolute;
    left: 0;
    top: 35px;
    bottom: 0;
    width: 200px; */
    background: rgba(255, 255, 255, 0.3);
    padding: 0.5em;
    overflow: auto;
    /* border-radius: 0.25em; */
}

.route.circle .split-container > .content
{
    overflow: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.route.circle .content > .data-circle
{
    margin: 2em auto;
}

.route.circle .gutter
{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: col-resize;
    font-size: 0.8em;
    background-color: rgba(255, 255, 255, 0.2);
    color: rgba(255, 255, 255, 0.4);
    transition: background-color 0.25s, color 0.25s;
}

.route.circle .gutter:hover
{
    background-color: rgba(255, 255, 255, 0.25);
    color: rgba(255, 255, 255, 0.5);
}

.route.circle .gutter-horizontal::after
{
    content: '|||';
}
