.progress-circle
{
    --band-width: 20px;
    position: relative;
    width: 100px;
    height: 100px;
    margin: 1em;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.5);
}

.progress-circle > .band,
.progress-circle > .band .band,
.progress-circle > .center
{
    position: absolute;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.8);
}

.progress-circle > .band
{
    width: 100%;
    height: 100%;
}

.progress-circle > .band .band
{
    top: calc(calc(var(--band-width) / 2) - 1px);
    left: calc(calc(var(--band-width) / 2) - 1px);
    width: calc(100% - var(--band-width));
    height: calc(100% - var(--band-width));
}

.progress-circle > .band .band.center
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: 1.1em;
    align-items: center;
    font-size: 0.7em;
    background-color: black;
    color: white;
}

.progress-circle > .band .band.center > .value
{
    font-size: 1.1em;
}
